<template>
	<section id="auth">
    <!-- <div class="wrapper"> -->
      <!-- <div class="image_overlay"> 
        <img src="/img/about.png"/>
				<h1>Who <br>we are</h1>
      </div> -->
    <!-- </div> -->
		<!-- <div class="wrapper2"></div> -->
		<div class="wrapper3 pb-3">
			<div class="container pb-md-5">
				<h3 class="pt-5 pb-3">Deposit Money</h3>
				<div class="card p-md-5">
					 <div class="mt-4 amount">
					  <p class="p2">Enter Amount & Choose Payment Gateways</p>
             <select  title="Pick a number" class="selectpicker form-control" name="paymentMethod" v-model="paymentMethod">
			  <option disabled value="">Select Payment Option</option>
              <option value="paystack">Paystack</option>
              <option value="vogue">Vogue Payment</option>
            </select> 
				  </div>
			</div>
        <div class="mt-4" v-if="paymentMethod =='paystack'">	
				<form>
					<input type="number" v-model="depositAmount" name="amount" placeholder="Enter Amount" class="form-control p-4">
					<input type="hidden" v-model="email" name="ref" class="form-control p-4">
					<p>You have requested to fund {{depositAmount}} NGN. click below to proceed to payment</p>
					<button  v-if="paymentMethod =='paystack'" @click.prevent="sendPost()" class="btn play mt-5 py-2 px-5">Proceed</button> 
				</form>
			</div>
			<div class="mt-4" v-if="paymentMethod =='vogue'">	
				<form>
					<input type="number" v-model="depositAmount" name="amount" placeholder="Enter Amount" class="form-control p-4">
					<br>
					<input type="text" v-model="memo" name="ref" placeholder="Enter Description" class="form-control p-4">
					<p>You have requested to fund {{depositAmount}} NGN. click below to proceed to payment</p>
					<button  v-if="paymentMethod =='vogue'" @click.prevent="sendPost1()" class="btn play mt-5 py-2 px-5">Proceed</button> 
				</form>
			</div>
				</div>
			</div>
		<!-- </div> -->
	</section>
</template>

<script> 
import Navigation from "@/components/Landing/Navigation.vue"; 
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex';
import axios from 'axios';

export default {
  name: "components",
  components: { Navigation },
  data(){
    return {  
      depositAmount:'',
	  paymentMethod: '',
	  email: '',
	  ref:'',
	  memo:''
    }
  },
  mounted () {

	},
 	methods: {
	async	sendPost() {
	let r = Math.random().toString(36).substring(7);

 const sendRequest = await axios.post('https://middlewarez.herokuapp.com/api/v1/payment/paystack', {
    reference: r,
	email: this.userDetails.email,
	username: this.userDetails.username,
	amount: this.depositAmount * 100
  });

window.location.href = sendRequest.data.message.data.authorization_url;
      
        this.$swal({
          type: "success",
          title: 'Transaction Initiated',
        });
        return;
      
},
	async sendPost1() {
     

 const sendRequest1 = await axios.post('https://middlewarez.herokuapp.com/api/v2/pay', {
    memo: this.memo,
	total: this.depositAmount,
	user_id: this.userDetails.email,
	cur: 'NGN'
  })
 window.location.href = sendRequest1.data.url;
 this.$swal({
          type: "success",
          title: 'Transaction Initiated',
        });
        return;
}
  },
	 computed: {
    ...mapState(["userDetails"])
  }
}
</script>
 
<style scoped>
