	background: #F4F4F4;
	margin-bottom:-10vh;
	min-height: 900px;
}
h3 {
	font-family: Graphik;
	font-size: 40px;
	line-height: normal;
	
	color: #21703A;
	font-weight: bolder;
	letter-spacing:-2px;
}
p {
	font-family: Graphik;
	font-size: 20px;
	line-height: 42px;
	color: #666666;
}
.wrapper {
	height:62vh;
}
.wrapper2 {
	height:10vh;	
	background-color:#21703A;
}
h1 {
	font-family: Graphik;
	font-size: 120px;
	line-height: 131px;
	color: #FFFFFF;
	font-weight: bolder;
	margin-top:25vh;
	margin-left:8%;
}
#auth img { 
  object-fit: cover;
	height: 100vh; 	
	width:100vw;
	position: absolute;
	top: 10%;
	bottom: 0;
	z-index: -2;
} 
#auth .image_overlay:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  top: 10%;
  left: 0;
  position: absolute; 
	background: rgba(20, 0, 5, 0.7);       
  z-index: -1;
}
@media screen and (max-width:1040px) {
