	height:40vh;
}
h1 {
	font-size: 70px;
	line-height: 60px;
	margin-top:15vh; 
}
#auth img { 
  object-position: 50%;
}
p {
	font-size: 16px; 
} 
}
#auth .play {
  background-color: #269A4C; 
  color:#fff; 
	font-size: 24px;
	text-transform: none;
	font-weight:bold; 
	box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.15);
	border-radius: 5px;
}
</style>
